* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #1a1a2e;
  color: #ffffff;
}

.main-menu {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  padding: 20px;
  position: relative;
  overflow: hidden;
}

/* Animated background particles */
.main-menu::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(2px 2px at 40px 60px, #fff 100%, transparent),
    radial-gradient(2px 2px at 20px 50px, #fff 100%, transparent),
    radial-gradient(2px 2px at 30px 100px, #fff 100%, transparent),
    radial-gradient(2px 2px at 40px 60px, #fff 100%, transparent),
    radial-gradient(2px 2px at 110px 90px, #fff 100%, transparent),
    radial-gradient(2px 2px at 190px 150px, #fff 100%, transparent);
  background-repeat: repeat;
  background-size: 200px 200px;
  animation: stars 8s linear infinite;
  opacity: 0.2;
  z-index: 1;
}

@keyframes stars {
  0% { transform: translateY(0); }
  100% { transform: translateY(-200px); }
}

.menu-header {
  text-align: center;
  padding: 60px 0 40px;
  position: relative;
  z-index: 2;
  margin-top: 10px;
}

.game-logo {
  width: 150px;
  height: 150px;
  margin: 0 auto -5px;
  border-radius: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  animation: logoFloat 3s ease-in-out infinite;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.logo-image {
  width: 90%;
  height: 90%;
  object-fit: contain;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
}

@keyframes logoFloat {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.game-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
  margin: 0 auto -20px;
}

.menu-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  padding: 0 10px;
  max-width: 500px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.menu-item {
  aspect-ratio: 1;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.menu-item:active {
  transform: scale(0.95);
  background: rgba(255, 255, 255, 0.15);
}

.menu-item-icon {
  font-size: 32px;
  margin-bottom: 10px;
  color: #ffd700;
  text-shadow: 0 2px 8px rgba(255, 215, 0, 0.3);
}

.menu-item-label {
  color: white;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
}

/* Shine effect on menu items */
.menu-item::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg,
    transparent 0%,
    rgba(255, 255, 255, 0.1) 50%,
    transparent 100%
  );
  transform: rotate(45deg);
  animation: shine 3s infinite;
  pointer-events: none;
}

@keyframes shine {
  0% { transform: translateX(-100%) rotate(45deg); }
  20%, 100% { transform: translateX(100%) rotate(45deg); }
}

/* Player stats bar */
.player-stats {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 3;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 12px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  backdrop-filter: blur(5px);
}

.stat-icon {
  font-size: 18px;
  color: #ffd700;
}

.stat-value {
  color: white;
  font-size: 16px;
  font-weight: bold;
  line-height: 24px;
}

/* Work timer overlay */
.work-timer-container {
  position: fixed;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  border-radius: 0 0 20px 20px;
  padding: 15px 25px;
  color: white;
  text-align: center;
  z-index: 3;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.timer-display {
  font-size: 20px;
  font-weight: bold;
  color: #4CAF50;
  margin: 10px 0;
}

.skip-time-button {
  background: #ff4757;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 15px;
  font-size: 12px;
  font-weight: bold;
  margin-top: 10px;
}

.skip-time-button:active {
  transform: scale(0.95);
}

/* Disabled state */
.menu-item.disabled {
  opacity: 0.5;
  pointer-events: none;
}

/* Notification badge */
.notification-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #ff4757;
  color: white;
  font-size: 12px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

/* Add smooth scrolling */
.main-menu {
  -webkit-overflow-scrolling: touch;
}

/* Improve touch targets */
.menu-item {
  min-height: 44px;
  min-width: 44px;
}

/* Tablet and larger screens */
@media (min-width: 768px) {
  .menu-grid {
    grid-template-columns: repeat(3, 1fr);
    max-width: 800px;
  }

  .game-logo {
    width: 180px;
    height: 180px;
  }

  .game-title {
    font-size: 3rem;
  }

  .coin-icon {
    width: 28px;
    height: 28px;
  }

  .stat-value {
    font-size: 18px;
    line-height: 28px;
  }
}

.coin-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
  vertical-align: middle;
}

.logo-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 10px;
}

.rank-indicator {
  display: flex;
  align-items: center;
  gap: 4px;
  margin-left: 8px;
  padding: 2px 8px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  font-size: 0.9em;
  transition: all 0.3s ease;
}

.rank-icon {
  font-size: 1.2em;
}
.rank-indicator .rank-icon {
  width: 29px;
  height: 29px;
  object-fit: contain;
}
.rank-name {
  font-weight: bold;
}
