.tournament-registration {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1100;
}

.registration-content {
  background: #1a1a2e;
  border-radius: 15px;
  padding: 30px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  color: white;
  animation: modalSlideUp 0.3s ease-out;
}

.tournament-info {
  margin: 20px 0;
}

.tournament-details {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 15px;
  margin: 15px 0;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
  padding: 5px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.prize-pool {
  background: rgba(255, 215, 0, 0.1);
  border-radius: 10px;
  padding: 15px;
  margin: 15px 0;
}

.prize-pool h4 {
  color: #ffd700;
  margin: 0 0 10px 0;
}

.prize-pool ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.prize-pool li {
  margin: 8px 0;
  font-size: 0.9em;
}

.registration-actions {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}

.register-button, .cancel-button {
  flex: 1;
  padding: 12px;
  border: none;
  border-radius: 25px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.register-button {
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
}

.register-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.3);
}

.register-button:disabled {
  background: #666;
  cursor: not-allowed;
}

.cancel-button {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.cancel-button:hover {
  background: rgba(255, 255, 255, 0.2);
}

.registration-error {
  background: rgba(255, 87, 87, 0.1);
  color: #ff5757;
  padding: 10px;
  border-radius: 5px;
  margin: 10px 0;
  text-align: center;
}

.registration-note {
  margin-top: 20px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  font-size: 0.9em;
  color: #ccc;
}

@keyframes modalSlideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .registration-content {
    width: 95%;
    padding: 20px;
  }

  .registration-actions {
    flex-direction: column;
  }
} 