.friends-list-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1100;
}

.friends-list-content {
  background: #1a1a2e;
  border-radius: 15px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation: modalSlideIn 0.3s ease-out;
}

.friends-list-header {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.friends-list-header h3 {
  margin: 0;
  color: white;
}

.close-button {
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 0 10px;
}

.friends-search {
  padding: 15px;
}

.friends-search input {
  width: 100%;
  padding: 10px;
  border-radius: 25px;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.friends-grid {
  padding: 15px;
  overflow-y: auto;
  flex-grow: 1;
}

.friend-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: transform 0.2s ease;
}

.friend-card:hover {
  transform: translateX(5px);
}

.friend-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.friend-info {
  flex-grow: 1;
  color: white;
}

.friend-info h4 {
  margin: 0 0 5px 0;
}

.friend-info p {
  margin: 0;
  font-size: 0.9em;
  color: #ccc;
}

.challenge-button {
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.challenge-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(76, 175, 80, 0.3);
}

.challenge-button:disabled {
  background: #666;
  cursor: not-allowed;
}

.no-friends {
  text-align: center;
  color: #ccc;
  padding: 30px;
}

@keyframes modalSlideIn {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .friends-list-content {
    width: 95%;
    max-height: 90vh;
  }

  .friend-card {
    flex-direction: column;
    text-align: center;
  }

  .friend-avatar {
    width: 60px;
    height: 60px;
  }
} 