.rank-display {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 15px;
  padding: 15px;
  color: white;
  width: fit-content;
}

.rank-badge {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.rank-icon {
  width: 38px;
  height: 38px;
  object-fit: contain;
}

.rank-info {
  display: flex;
  flex-direction: column;
}

.rank-name {
  font-weight: bold;
  font-size: 18px;
}

.rank-elo {
  font-size: 14px;
  opacity: 0.8;
}

.rank-progress {
  margin-top: 10px;
}

.progress-bar {
  height: 6px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  transition: width 0.3s ease;
}

.progress-text {
  font-size: 12px;
  margin-top: 5px;
  text-align: center;
  opacity: 0.8;
}

/* Size variants */
.rank-display.small .rank-icon {
  width: 29px;
  height: 29px;
}

.rank-display.small .rank-name {
  font-size: 14px;
}

.rank-display.small .rank-elo {
  font-size: 12px;
}

.rank-display.large .rank-icon {
  width: 58px;
  height: 58px;
}

.rank-display.large .rank-name {
  font-size: 24px;
}

.rank-display.large .rank-elo {
  font-size: 18px;
}

/* Animation for rank up */
@keyframes rankUp {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.rank-display.rank-up .rank-badge {
  animation: rankUp 0.5s ease;
} 