.pvpselect-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  padding: 20px;
  position: relative;
  overflow: hidden;
}

/* Animated background effect */
.pvpselect-container::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 30%, rgba(41, 196, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 41, 112, 0.1) 0%, transparent 50%);
  pointer-events: none;
  z-index: 1;
}

.pvpselect-back-button {
  position: fixed;
  top: 20px;
  left: 20px;
  padding: 12px 24px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 30px;
  color: white;
  font-weight: bold;
  backdrop-filter: blur(5px);
  z-index: 10;
  transition: all 0.3s ease;
}

.pvpselect-back-button:active {
  transform: scale(0.95);
  background: rgba(255, 255, 255, 0.15);
}

.pvpselect-title {
  color: white;
  font-size: 24px;
  margin: 60px 0 30px;
  text-align: center;
  font-weight: bold;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  z-index: 2;
  position: relative;
}

.pvpselect-modes {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  padding: 0 15px;
  max-width: 500px;
  margin: 0 auto;
  z-index: 2;
  position: relative;
}

.pvpselect-mode {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  overflow: hidden;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  position: relative;
}

.pvpselect-mode:active {
  transform: scale(0.98);
}

.pvpselect-mode-image {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 20px 20px 0 0;
}

.pvpselect-mode-content {
  padding: 20px;
}

.pvpselect-mode-name {
  color: white;
  font-size: 20px;
  margin: 0 0 10px;
  font-weight: bold;
}

.pvpselect-mode-description {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  margin-bottom: 15px;
  line-height: 1.4;
}

.pvpselect-mode-requirement {
  color: #ff6b6b;
  font-size: 14px;
  margin-bottom: 10px;
}

.pvpselect-mode-xp {
  color: #4CAF50;
  font-size: 14px;
  font-weight: bold;
}

.pvpselect-mode.locked {
  opacity: 0.7;
}

.pvpselect-mode.locked::after {
  content: '🔒';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.pvpselect-start-battle-button {
  position: fixed;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  padding: 16px 32px;
  background: linear-gradient(45deg, #4CAF50, #45a049);
  border: none;
  border-radius: 30px;
  color: white;
  font-weight: bold;
  font-size: 16px;
  box-shadow: 0 4px 15px rgba(76, 175, 80, 0.3);
  width: 80%;
  max-width: 300px;
  z-index: 10;
  opacity: 0;
  transition: all 0.3s ease;
}

.pvpselect-start-battle-button.visible {
  opacity: 1;
  transform: translateX(-50%) translateY(0);
}

.pvpselect-start-battle-button:active {
  transform: translateX(-50%) scale(0.98);
}

.pvpselect-error-message {
  position: fixed;
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 87, 87, 0.9);
  color: white;
  padding: 15px 30px;
  border-radius: 30px;
  font-size: 14px;
  backdrop-filter: blur(5px);
  z-index: 10;
  width: 80%;
  max-width: 300px;
  text-align: center;
  box-shadow: 0 4px 15px rgba(255, 87, 87, 0.3);
}

.matchmaking-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.matchmaking-spinner {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-left-color: #4CAF50;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

.matchmaking-overlay p {
  color: white;
  font-size: 18px;
  margin-bottom: 30px;
}

.matchmaking-overlay button {
  padding: 12px 24px;
  background: #ff5757;
  border: none;
  border-radius: 25px;
  color: white;
  font-weight: bold;
  transition: all 0.3s ease;
}

.matchmaking-overlay button:active {
  transform: scale(0.95);
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Tablet and larger screens */
@media (min-width: 768px) {
  .pvpselect-modes {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    max-width: 1000px;
  }

  .pvpselect-mode-image {
    height: 200px;
  }

  .pvpselect-title {
    font-size: 32px;
    margin: 80px 0 40px;
  }
}

/* Add smooth scrolling */
.pvpselect-container {
  -webkit-overflow-scrolling: touch;
}

/* Improve touch targets */
.pvpselect-back-button,
.pvpselect-start-battle-button,
.matchmaking-overlay button {
  min-height: 44px;
  min-width: 44px;
}
